import { Component, Prop, State, Event, EventEmitter, h, Element, Watch, AttachInternals } from '@stencil/core';
import { syncCheckableFormValue, validateRequired } from '../../utils/form';

export type CheckboxSize = 'sm' | 'md';

@Component({
  tag: 'bh-checkbox',
  styleUrl: 'bh-checkbox.css',
  shadow: true,
  formAssociated: true,
})
export class BhCheckbox {
  @Element() el!: HTMLElement;
  @AttachInternals() internals!: ElementInternals;

  private inputRef?: HTMLInputElement;
  private uniqueId = `bh-checkbox-${Math.random().toString(36).substring(2, 9)}`;

  /** Checkbox size */
  @Prop() size: CheckboxSize = 'md';

  /** Checked state */
  @Prop({ mutable: true, reflect: true }) checked: boolean = false;

  /** Indeterminate state */
  @Prop({ mutable: true }) indeterminate: boolean = false;

  /** Disabled state */
  @Prop() disabled: boolean = false;

  /** Name attribute for form submission */
  @Prop() name: string = '';

  /** Value attribute for form submission */
  @Prop() value: string = 'on';

  /** Optional label text - when provided, renders checkbox with label */
  @Prop() label: string = '';

  /** Optional supporting text below the label */
  @Prop() supportingText: string = '';

  /** Whether the checkbox is required */
  @Prop() required: boolean = false;

  /** Default checked state for form reset */
  @Prop() defaultChecked: boolean = false;

  /** Internal hover state */
  @State() isHovered: boolean = false;

  /** Internal focus state */
  @State() isFocused: boolean = false;

  /** Emitted when checkbox state changes via user interaction */
  @Event() bhChange!: EventEmitter<boolean>;

  @Watch('checked')
  handleCheckedChange(newValue: boolean) {
    if (this.inputRef) {
      this.inputRef.checked = newValue;
    }
  }

  @Watch('indeterminate')
  handleIndeterminateChange(newValue: boolean) {
    if (this.inputRef) {
      this.inputRef.indeterminate = newValue;
    }
  }

  componentDidLoad() {
    if (this.inputRef) {
      this.inputRef.indeterminate = this.indeterminate;
    }
    syncCheckableFormValue(this.internals, this.name, this.checked, this.value);
    validateRequired(this.internals, this.required, this.checked);
  }

  formResetCallback() {
    this.checked = this.defaultChecked;
    this.indeterminate = false;
    if (this.inputRef) {
      this.inputRef.checked = this.defaultChecked;
      this.inputRef.indeterminate = false;
    }
    syncCheckableFormValue(this.internals, this.name, this.checked, this.value);
    validateRequired(this.internals, this.required, this.checked);
  }

  private handleChange = (event: Event) => {
    event.stopPropagation();
    if (this.disabled) return;

    const input = event.target as HTMLInputElement;

    if (this.indeterminate) {
      this.indeterminate = false;
      this.checked = false;
      if (this.inputRef) {
        this.inputRef.indeterminate = false;
        this.inputRef.checked = false;
      }
    } else {
      this.checked = input.checked;
    }

    syncCheckableFormValue(this.internals, this.name, this.checked, this.value);
    validateRequired(this.internals, this.required, this.checked);
    this.bhChange.emit(this.checked);
  };

  private handleMouseEnter = () => {
    this.isHovered = true;
  };

  private handleMouseLeave = () => {
    this.isHovered = false;
  };

  private handleFocus = () => {
    this.isFocused = true;
  };

  private handleBlur = () => {
    this.isFocused = false;
  };

  private handleControlClick = () => {
    if (this.disabled) return;
    this.inputRef?.click();
  };

  private renderCheckboxControl() {
    const isCheckedState = this.checked || this.indeterminate;

    const checkboxClasses = {
      'checkbox': true,
      [`checkbox-${this.size}`]: true,
      'checkbox-checked': isCheckedState,
      'checkbox-disabled': this.disabled,
      'checkbox-focused': this.isFocused && !this.disabled,
      'checkbox-hover': this.isHovered && !this.disabled,
    };

    return (
      <div
        class="checkbox-control"
        onClick={this.handleControlClick}
        onMouseEnter={this.handleMouseEnter}
        onMouseLeave={this.handleMouseLeave}
      >
        <input
          ref={(el) => (this.inputRef = el)}
          type="checkbox"
          id={this.uniqueId}
          name={this.name}
          value={this.value}
          checked={this.checked}
          disabled={this.disabled}
          class="checkbox-input"
          onChange={this.handleChange}
          onFocus={this.handleFocus}
          onBlur={this.handleBlur}
          aria-describedby={this.supportingText ? `${this.uniqueId}-description` : undefined}
        />
        <div class={checkboxClasses} part="checkbox" aria-hidden="true">
          {this.checked && !this.indeterminate && (
            <span class="material-symbols-outlined checkbox-icon check-icon">
              check
            </span>
          )}
          {this.indeterminate && (
            <span class="material-symbols-outlined checkbox-icon minus-icon">
              remove
            </span>
          )}
        </div>
      </div>
    );
  }

  render() {
    const hasLabel = this.label || this.supportingText;

    const wrapperClasses = {
      'checkbox-wrapper': true,
      'checkbox-wrapper-with-label': !!hasLabel,
      'checkbox-wrapper-disabled': this.disabled,
    };

    // Render with bh-label when label/supportingText is provided
    if (hasLabel) {
      return (
        <div class={wrapperClasses} part="wrapper">
          <bh-label
            layout="inline-start"
            label={this.label}
            supportingText={this.supportingText}
            disabled={this.disabled}
            for={this.uniqueId}
          >
            {this.renderCheckboxControl()}
          </bh-label>
        </div>
      );
    }

    // Standalone checkbox without label
    return (
      <div class={wrapperClasses} part="wrapper">
        {this.renderCheckboxControl()}
      </div>
    );
  }
}
